<template>
  <div class="jrServicesList">
    <div class="page-header-main">
      <mt-header>
        <a @click="back" slot="left">
          <mt-button icon="back">计日类服务列表</mt-button>
        </a>
      </mt-header>
    </div>
    <div class="jrServicesListBody">
      <div class="jrServicesListScroe">
        <p class="first">您被护理人评为</p>
        <p class="score"><span>{{globalVal.evaluateScore}}</span>分</p>
      </div>
      <div class="serviecsList">
        <div class="recommendsServicesList">
          <div class="recommendsService" v-for="recommend in recommends">
            <div class="recommendsServiceItem">
              <div @click="handleClick(recommend.id,recommend.title, recommend.price,recommend.duration)">
                <p class="title">{{recommend.title}}</p>
              </div>
              <div class="info">
                <p class="day">{{recommend.price}}元/天</p>
                <p class="showDetail" @click="showDetailFn($event, recommend.title)">查看详情</p>
              </div>
            </div>
            <div class="recommendsServiceDetail" :ref="recommend.title"> 
              <ul>
                <li v-for="desc in recommend.desc"><span></span>{{desc}}</li>
              </ul>
            </div>
          </div>
        </div>
        <div class="othersServicesList">
          <div class="othersService" v-for="other in others">
            <div class="othersServiceItem">
              <div @click="handleClick(other.id,other.title, other.price,other.duration)">
                <p class="difftitle">{{other.title}}</p>
              </div>
              <div class="info">
                <p class="day">{{other.price}}元/天</p>
                <p class="showDetail" @click="showDetailFn($event, other.title)">查看详情</p>
              </div>
            </div>
            <div class="othersServiceDetail" :ref="other.title">
              <ul>
                <li v-for="desc in other.desc"><span></span>{{desc}}</li>
              </ul>
            </div>
          </div>
          <!-- <div class="othersService" v-for="other in others" @click="handleClick(other.id)">
            <div class="othersServiceItem">
             <p>{{other.title}}  {{other.price}}</p>
            </div>
            <div class="othersServiceDetail">
              <ul>
                <li v-for="desc in other.desc">{{desc}}</li>
              </ul>
            </div>
          </div> -->
        </div>
      </div>
    </div>
  </div>
</template>
  </div>
<script>
import { mapGetters, mapActions} from 'vuex'
export default {
  name: 'jrServicesList',
  methods: {
    ...mapActions({
      fetchJrTypeServicesList: 'fetchJrTypeServicesList',
      changeCompanyType: 'changeCompanyType'
    }),
    back: function() {
      history.go(-1);
    },
    handleClick: function(id, title, price, duration) {
      this.$router.push({path: '/appointment', query: { id: id, type: 'jr', duration: duration}})
      this.changeCompanyType(title + ' ' + price);
    },
    showDetailFn: function(e, name) {
      if(e.target.style.backgroundImage == '' || e.target.style.backgroundImage == 'url("/static/detailDown.png")') {
        this.$refs[name][0].style.display = 'block';
        e.target.style.backgroundImage = 'url("/static/detailUp.png")';
      } else {
        this.$refs[name][0].style.display = 'none';
        e.target.style.backgroundImage = 'url("/static/detailDown.png")';
      }
    }
  },
  computed: {
    ...mapGetters({
      globalVal: 'getGlodalVal',
      recommends: 'getRecommends',
      others: 'others'
    })
  },
  created() {
    const params = {
      score: this.globalVal.evaluateScore,
      city: this.globalVal.currentCity
    }
    this.fetchJrTypeServicesList(params).catch(error => {
      if(error.body.code == 3001) {
        this.$router.push('/login');
      }
    });
  }
}
</script>
<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
.jrServicesList .mint-button-text {
  padding-right: 0.5rem;
}
.jrServicesList {
  background-color: #e3e1e3;
}
.jrServicesListScroe {
  background: url(../assets/bg.png) no-repeat; 
  background-size: 100%;
  height: 3.5rem;
  background-position: bottom;  
  background-color: #fff;
  padding-top: 0.3rem;
}
.jrServicesListScroe .first {
  font-size: 0.4rem;
}
.score {
  text-align: center;
  font-size: 0.3rem;
  color: #d06091;
}
.score span {
  font-size: 2rem;
}
.jrServicesList .serviecsList {
  font-size: 0.4rem;
  background: #e3e1e3;
}
.jrServicesList .recommendsService {
  margin: 0.2rem 0;
}
.jrServicesList .recommendsServiceItem {
  display: flex;
  display: -ms-flexbox;
  justify-content: space-between;
  height: 1.5rem;
  background-color: #fff;
}
.jrServicesList .othersService {
  margin: 0.2rem 0;
}
.jrServicesList .othersServiceItem {
  display: flex;
  display: -ms-flexbox;
  justify-content: space-between;
  height: 1.5rem;
  background-color: #fff;
}
.jrServicesList .othersServiceItem {
 /* display: flex;
  display: -ms-flexbox;
  height: 1.2rem;*/
  background-color: #fff;
}
.jrServicesList .title{
  height: 1.5rem;
  line-height: 1.5rem;
  margin-left: 0.4rem;
  padding-left: 1rem;
  background: url(../assets/tag.png) no-repeat;
  background-position: left;
  background-size: contain;
  text-indent: 0.3rem;
  color: #565256;
}
.jrServicesList .info {
  margin-right: 0.4rem;
}
.jrServicesList .info .day {
  line-height: 1rem;
  height: 1rem;
  color: #ff8f0b;
}
.jrServicesList .info .showDetail {
  font-size: 0.3rem;
  color: #565256;
  background-size: contain;
  background-image: url(../assets/detailDown.png);
  background-repeat: no-repeat;
  background-position: right;
}
.jrServicesList .recommendsServiceDetail {
  display: none;
  margin-left: 0.4rem;
}
.jrServicesList li span {
  width: 0.2rem;
  height: 0.2rem;
  background: #fa73ae;
  display: inline-block;
  border-radius: 0.1rem;
  vertical-align: middle;
  margin-right: 0.3rem;
}
.jrServicesList li {
  list-style: none;
  height: 0.7rem;
  line-height: 0.7rem;
  color: #a19ba1;
  font-size: 0.3rem;
}
.jrServicesList .difftitle {
  height: 1.5rem;
  line-height: 1.5rem;
  margin-left: 0.4rem;
  color: #565256;
}
.jrServicesList .othersServiceDetail {
  display: none;
  margin-left: 0.4rem;
}
</style>



